import React from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';

import { Button, Modal } from 'antd';
import { WeaTableNew } from 'comsMobx';
import { WeaTop, WeaTab, WeaRightMenu, WeaDialog } from 'ecCom';

import { renderNoright, getSearchs } from '../util'; // formutil
import { doEdit } from '../apis';

const WeaTable = WeaTableNew.WeaTable;

@inject('editTableStore')
@observer
export default class BaseTable extends React.Component {
  componentWillMount() { // 
    const { editTableStore: { doInit } } = this.props;
    doInit();
  }

  componentWillReceiveProps(nextProps) {
    const { editTableStore } = this.props;
    if (this.props.location.key !== nextProps.location.key) { //  
      editTableStore.doInit();
    }
  }

  // 
  showColumn = () => {
    const { editTableStore: { tableStore} } = this.props;
    tableStore.setColSetVisible(true);
    tableStore.tableColSet(true);
  }

  // 
  onAdd = () => {
    const { editTableStore: { setState } } = this.props;
    setState({
      isCreate: true,
      editId: '',
      visible: true,
    });
  }

  // 
  onEdit = (record) => {
    const { editTableStore: { setState, dialogForm } } = this.props;
    setState({
      isCreate: false,
      editId: record.id,
      visible: true,
    });
    dialogForm.updateFields({
      requestname: { // form
        value: record.requestname,
      },
      creater: { // :-- demo
        value: record.creater,
        valueSpan: record.createrspan,
        valueObj: {
          id: record.creater,
          name: record.createrspan,
        }
      },
      state: { // 
        value: record.state,
      },
      type: { // 
        value: record.type,
      },
      creatertime: { // 
        value: record.creatertime,
      },
    })
  }

  // 
  onCancel = () => {
    const { editTableStore: { setState, dialogForm, dialogFormCondition, initDialogForm } } = this.props;
    setState({
      isCreate: true,
      editId: '',
      visible: false,
    });
    initDialogForm();
  }

  onOperatesClick = (record, index, operate, flag) => {
    const { editTableStore: { doDelete } } = this.props;
    switch(operate.index.toString()){
      case '0': // 
        this.onEdit(record);
        break;
      case '1': // 
        Modal.confirm({
          title: '',
          content: '?',
          onOk: () => {
            doDelete(record.id)
          },
        });
        break;
      default:
        break;
    }
  };

  showColumn = () => {
    const { baseTableStore: { tableStore } } = this.props;
    tableStore.setColSetVisible(true);
    tableStore.tableColSet(true);
  }

  // columns
  getColumns = (columns) => {
    let newColumns = '';
    newColumns = columns.map(column => {
      let newColumn = column;
      newColumn.render = (text, record, index) => { //
        let valueSpan = record[newColumn.dataIndex + "span"] !== undefined ? record[newColumn.dataIndex + "span"] : record[newColumn.dataIndex];
        return (
          newColumn.dataIndex == 'requestname' ?
            <a onClick={() => {this.onEdit(record)}}
               dangerouslySetInnerHTML={{ __html: valueSpan }} />
            :
            <div dangerouslySetInnerHTML={{ __html: valueSpan }} />
        )
      }
      return newColumn;
    });
    return newColumns;
  }
  
  render() {
    const { editTableStore } = this.props;
    const { loading, hasRight, form, condition, tableStore, showSearchAd, isCreate, visible, dialogForm, dialogFormCondition,
      getTableDatas, doSearch, setShowSearchAd, doDialogSave, doDialogCancel, doDelete, doSave
    } = editTableStore;
    const selectedRowKeys = toJS(tableStore.selectedRowKeys) || []; // tableStore 

    if (!hasRight && !loading) { // 
      return renderNoright();
    }

    const rightMenu = [// 
      {
        key: 'BTN_NEW',
        icon: <i className='icon-coms-New-Flow'/>,
        content : '',
        onClick : this.onAdd
      },
      {
        key: 'BTN_DEL',
        icon: <i className='icon-coms-delete'/>,
        content : '',
        disable: selectedRowKeys.length === 0, // 
        onClick : doDelete,
      },
      {
        key: 'BTN_COLUMN',
        icon: <i className='icon-coms-Custom'/>,
        content : '',
        onClick : this.showColumn
      },
    ];
    const collectParams = { // 
      favname: '',
      favouritetype: 1,
      objid: 0,
      link: 'wui/index.html#/ns_demo06/index',
      importantlevel: 1,
    };
    const adBtn = [ // 
      <Button type="primary" onClick={doSearch}></Button>,
      <Button type="ghost" onClick={() => form.resetForm()}></Button>,
      <Button type="ghost" onClick={() => setShowSearchAd(false)}></Button>,
    ];
    const btns = [ // 
      <Button type='primary' onClick={this.onAdd}></Button>,
      <Button type='primary' disable={selectedRowKeys.length === 0} onClick={this.delete}></Button>,
    ];

    return (
      <div style={{ height: '100%' }}>
        <WeaRightMenu
          datas={rightMenu} // 
          collectParams={collectParams} // :   3
        >
          <WeaTop
            title="" // 
            icon={<i className='icon-coms-meeting' />} // 
            iconBgcolor='#F14A2D' // 
            buttons={btns} // 
            buttonSpace={10} // 
            showDropIcon={true} // 
            dropMenuDatas={rightMenu} // 
            dropMenuProps={{ collectParams }} // :   3
          >
            <WeaTab
              searchType={['base', 'advanced']} // base advanced
              showSearchAd={showSearchAd} // 
              setShowSearchAd={bool => setShowSearchAd(bool)} //
              searchsAd={getSearchs(form, toJS(condition), 2)} // 
              buttonsAd={adBtn} // 
              onSearch={getTableDatas} // 
              onSearchChange={v => form.updateFields({ requestname: v })} // : 
              searchsBaseValue={form.getFormParams().requestname} // input: requestname
            />
            <WeaTable // tableloading
              comsWeaTableStore={tableStore} // table store
              hasOrder={true} // 
              needScroll={true} // table
              getColumns={this.getColumns} // ()
              onOperatesClick={this.onOperatesClick.bind(this)} // 
            />
          </WeaTop>
        </WeaRightMenu>
        <WeaDialog
          style={{width: 750, height: 200 }} // Dialog
          title={isCreate ? '' : '' } // Dialog
          icon='icon-coms-meeting' // Dialog
          iconBgcolor='#F14A2D' // Dialog
          visible={visible} // Dialog
          buttons={[<Button type='primary' onClick={doSave}></Button>,]} // Dialog 
          onCancel={this.onCancel} //  Dialog 
          moreBtn={{ // ,  WeaMoreButton 
            datas: [{
              key: 'BTN_SAVE',
              icon: <i className='icon-coms-Preservation'/>,
              content : '',
              onClick : doSave,
            }],
            collectParams: collectParams
          }}
        >
          {// 
            getSearchs(dialogForm, dialogFormCondition, 1, true)
          }
        </WeaDialog>
      </div>
    )
  }
}


