import { observable, action, toJS } from 'mobx';
import { message } from 'antd';
import { WeaForm, WeaTableNew } from 'comsMobx';

import * as API from '../apis'; // API

const { TableStore } = WeaTableNew;

export class EditTableStore {
  @observable tableStore = new TableStore(); // new table
  @observable form = new WeaForm(); // nrew form
  @observable condition = []; // form
  @observable hasRight = true; //  
  @observable showSearchAd = false; // 

  @observable dialogForm = new WeaForm(); // 
  @observable dialogFormCondition = []; // form
  @observable visible = false; // 
  @observable editId = ''; // id

  // 
  @action
  doInit = () => {
    this.getCondition();
    this.getTableDatas();
    this.getDialogCondition();
  }

  // observable
  @action
  setState = (params) => {
    Object.keys(params).forEach(key => this[key] = params[key]);
  }

  // 
  @action
  getCondition = () => {
    API.getCondition().then(action(res => {
      if (res.api_status) { // /
        this.condition = res.condition;
        this.form.initFormFields(res.condition); // form
      } else {
        message.error(res.msg || '')
      }
    }));
  }

  // table
  @action
  getTableDatas = (toFirstPage = true) => { // toFirstPage:  
    const formParams = this.form.getFormParams() || {};
    API.getTableDatas(formParams).then(action(res => {
      if (res.api_status) { // /
        toFirstPage ? this.tableStore.getDatas(res.datas, 1) : this.tableStore.getDatas(res.datas); // table 
        this.hasRight = res.hasRight;
      } else {
        message.error(res.msg || '')
      }
    }));
  }

  // form
  @action
  getDialogCondition = () => {
    API.getDialogCondition().then(res => {
      if (res.api_status) { // /
        this.dialogFormCondition = res.condition;
        this.dialogForm.initFormFields(res.condition); // form
      } else {
        message.error(res.msg || '')
      }
    });
  }

  @action
  setShowSearchAd = bool => this.showSearchAd = bool;

  //  - 
  @action
  doSearch = () => {
    this.getTableDatas();
    this.showSearchAd = false;
  }

  // /
  @action
  doSave = () => {
    this.dialogForm.validateForm().then(action(f=>{ // : dialogFormvalidateForm
      if (f.isValid) { // : 
        const params = this.dialogForm.getFormParams();
        if (this.isCreate) {
          this.doAdd(params);
        } else {
          const editParams = {
            ...params,
            id: this.editId,
          }
          this.doEdit(editParams);
        }
        this.visible = false;
      } else {
        f.showErrors();
      }
    }));
  }

  // 
  doAdd = (params) => {
    API.doAdd(params).then(action(res => {
      if (res.api_status) {
        this.getTableDatas(false);
        this.initDialogForm();
      } else {
        message.error(res.msg || '');
      }
    }));
  }

  // 
  doEdit = (editParams) => {
    API.doEdit(editParams).then(action(res => {
      if (res.api_status) {
        this.getTableDatas(false);
        this.initDialogForm();
      } else {
        message.error(res.msg || '');
      }}
    ));
  }

  @action
  initDialogForm = () => {
    this.dialogForm.resetForm();
    this.dialogForm.initFormFields(toJS(this.dialogFormCondition));
  }

  // 
  @action
  doDelete = (id) => {
    const params = {
      id: id || toJS(this.tableStore.selectedRowKeys).join(',')
    }
    API.doDel(params).then(res => {
      if (res.api_status) {
        this.getTableDatas(false);
      } else {
        message.error(res.msg || '');
      }
    });
  }

}


