import { Spin } from 'antd';
import { WeaSwitch } from 'comsMobx';
import { WeaLocaleProvider, WeaAlertPage, WeaSearchGroup, WeaFormItem } from 'ecCom';
const getLabel = WeaLocaleProvider.getLabel;

// form: form
export const getSearchs = (form, condition, col, isCenter) => {
  const { isFormInit } = form;
  const formParams = form.getFormParams();
  let group = [];
  isFormInit && condition && condition.map(c =>{
    let items = [];
    c.items.map(fields => {
      items.push({
        com:(
          <WeaFormItem
            label={`${fields.label}`} // label 
            labelCol={{span: `${fields.labelcol}`}} // label
            wrapperCol={{span: `${fields.fieldcol}`}} // 
            error={form.getError(fields)} // : ,
            tipPosition="bottom" // : top/bottom
          >
            <WeaSwitch
              fieldConfig={fields}
              form={form}
              formParams={formParams}
            />
          </WeaFormItem>),
        colSpan:1,
      })
    });
    group.push(
      <WeaSearchGroup
        col={col || 1} // 
        needTigger={true} // 
        title={c.title || ''} // 
        showGroup={c.defaultshow} // 
        items={items} // 
        center={isCenter || false} // 
      />)
  });
  return group;
}

// 
export const renderLoading = (loading) => (
  <div className="wea-demo-loading">
    <Spin spinning={loading} />
  </div>
)
  
// 
export const renderNoright = () => (
  <WeaAlertPage>
    <div>
      {getLabel(2012,'')}
    </div>
  </WeaAlertPage>
)

// 
export const renderNoData = () => (
  <WeaAlertPage>
    <div>
      
    </div>
  </WeaAlertPage>
)


